.overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgb(43 43 43 / 80%);
  z-index: 1200;
}

.modalContent {
  max-width: calc(100vw - 48px);
  max-height: calc(100vh - 24px);
}
